<template>
  <div id="account">
    <div v-show="isInfoEdit">
      <div class="weui-msg">
        <div class="weui-msg__icon-area"><i class="weui-icon-info weui-icon_msg"></i></div>
        <h2 class="weui-msg__title"></h2>
        <p class="weui-msg__desc"></p>
        <div class="weui-msg__opr-area">
          <div class="weui-cells weui-cells_form">
            <div class="weui-cell">
              <div class="weui-cell__hd">
                <label class="weui-label"></label>
              </div>
              <div class="weui-cell__bd">
                <input class="weui-input" type="text" placeholder="" v-model="reName">
              </div>
            </div>
            <div class="weui-cell">
              <div class="weui-cell__hd">
                <label class="weui-label"></label>
              </div>
              <div class="weui-cell__bd">
                <input class="weui-input" type="number" placeholder="" v-model="rePhone">
              </div>
            </div>
            <div class="weui-cell">
              <div class="weui-cell__hd">
                <label class="weui-label"></label>
              </div>
              <div class="weui-cell__bd">
                <input class="weui-input" type="password" placeholder="" v-model="passwordForCheck">
              </div>
            </div>
          </div>
        </div>
        <div class="weui-msg__opr-area">
          <p class="weui-btn-area">
            <a @click="userUpdateInfo" class="weui-btn weui-btn_primary"></a>
            <a @click="infoEditClick" class="weui-btn weui-btn_warn"></a>
          </p>
        </div>
      </div>
    </div>
    <div v-show="isPasswordEdit">
      <div class="weui-msg">
        <div class="weui-msg__icon-area"><i class="weui-icon-warn weui-icon_msg-primary"></i></div>
        <h2 class="weui-msg__title"></h2>
        <p class="weui-msg__desc">123456</p>
        <div class="weui-msg__opr-area">
          <div class="weui-cells weui-cells_form">
            <div class="weui-cell">
              <div class="weui-cell__hd">
                <label class="weui-label"></label>
              </div>
              <div class="weui-cell__bd">
                <input class="weui-input" type="password" placeholder="" v-model="passwordForCheck">
              </div>
            </div>
            <div class="weui-cell">
              <div class="weui-cell__hd">
                <label class="weui-label"></label>
              </div>
              <div class="weui-cell__bd">
                <input class="weui-input" type="password" placeholder="" v-model="newPassword">
              </div>
            </div>
            <div class="weui-cell">
              <div class="weui-cell__hd">
                <label class="weui-label"></label>
              </div>
              <div class="weui-cell__bd">
                <input class="weui-input" type="password" placeholder="" v-model="newPasswordForCheck">
              </div>
            </div>
          </div>
        </div>
        <div class="weui-msg__opr-area">
          <p class="weui-btn-area">
            <a @click="userUpdatePassword" class="weui-btn weui-btn_primary"></a>
            <a @click="passwordEditClick" class="weui-btn weui-btn_warn"></a>
          </p>
        </div>
      </div>
    </div>
    <div v-show="!isInfoEdit && !isPasswordEdit">
      <div class="weui-cells__title"></div>
      <div class="weui-cells weui-cells_form">
        <div class="weui-cell">
          <div class="weui-cell__hd">
            <label class="weui-label"></label>
          </div>
          <div class="weui-cell__bd">{{ user.department }}</div>
        </div>
        <div class="weui-cell">
          <div class="weui-cell__hd">
            <label class="weui-label"></label>
          </div>
          <div class="weui-cell__bd">{{ user.reName ? user.reName : '' }}</div>
        </div>
        <div class="weui-cell">
          <div class="weui-cell__hd">
            <label class="weui-label"></label>
          </div>
          <div class="weui-cell__bd">{{ user.rePhone ? user.rePhone : '' }}</div>
        </div>
        <div class="weui-cell">
          <div class="weui-cell__hd">
            <label class="weui-label"></label>
          </div>
          <div class="weui-cell__bd">{{ user.wallet || 0 }}</div>
        </div>
      </div>
      <div class="weui-cells__title"></div>
      <div class="weui-cells">
        <a class="weui-cell weui-cell_access" @click="infoEditClick">
          <div class="weui-cell__hd"><label class="weui-label"></label></div>
          <div class="weui-cell__bd"></div>
          <div class="weui-cell__ft"></div>
        </a>
        <a class="weui-cell weui-cell_access" @click="passwordEditClick">
          <div class="weui-cell__hd"><label class="weui-label"></label></div>
          <div class="weui-cell__bd"></div>
          <div class="weui-cell__ft"></div>
        </a>
      </div>
      <div class="weui-cells__title"></div>
      <div class="weui-cells">
        <a class="weui-cell weui-cell_access" v-for="materialBook in materialBookList" @click="materialBookUpdateFail(materialBook._id)">
          <div class="weui-cell__bd">
            <span class="highlight">{{ materialBook.activity }}</span><br>
            {{ materialBook.name }} (<a :href="'tel:' + materialBook.phone">{{ materialBook.phone }}</a>)<br>
            {{ materialBook.takeDate }}<br>
            <span class="highlight">{{ materialBook.returnDate }}</span><br>
            <label v-for="(book, index) of materialBook.book"><b>({{ index + 1 }})&nbsp{{ book.name }}{{ book.book }}{{ book.unit }}&nbsp&nbsp</b></label><br>
            <span class="highlight"> {{ materialBook.sum || 0 }}</span>&nbsp&nbsp&nbsp&nbsp<span class="highlight">{{ materialBook.condition }}</span>
          </div>
          <div class="weui-cell__ft"></div>
        </a>
        <div class="weui-cell" v-show="!materialBookList.length">
          <div class="weui-cell__bd"></div>
        </div>
      </div>
      <div class="weui-cells__title"></div>
      <div class="weui-cells">
        <a class="weui-cell weui-cell_access" v-for="meetingBook in meetingBookList" @click="meetingBookUpdateFail(meetingBook._id)">
          <div class="weui-cell__bd">
            <span class="highlight">{{ meetingBook.activity }}</span><br>
            {{ meetingBook.name }} (<a :href="'tel:' + meetingBook.phone">{{ meetingBook.phone }}</a>)<br>
            {{ meetingBook.date }}<br>
            <span class="highlight">{{ meetingBook.time }}</span>
          </div>
          <div class="weui-cell__ft"></div>
        </a>
        <div class="weui-cell" v-show="!meetingBookList.length">
          <div class="weui-cell__bd"></div>
        </div>
      </div>
      <div class="weui-msg__opr-area">
        <p class="weui-btn-area">
          <a @click="logout" class="weui-btn weui-btn_warn"></a>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import api from '../api'
import logmsg from './logmsg'
import weui from 'weui.js'
export default {
  components: {
    logmsg
  },
  data () {
    return {
      userId: JSON.parse(window.atob(this.$store.state.token.split('.')[1])).id,
      user: {},
      reName: '',
      rePhone: '',
      passwordForCheck: '',
      newPassword: '',
      newPasswordForCheck: '',
      isInfoEdit: false,
      isPasswordEdit: false,
      materialBookList: [],
      meetingBookList: []
    }
  },
  methods: {
    infoEditClick () {
      this.isInfoEdit = !this.isInfoEdit
      this.newPassword = ''
      this.reName = ''
      this.rePhone = ''
      this.passwordForCheck = ''
      this.newPasswordForCheck = ''
    },
    passwordEditClick () {
      this.isPasswordEdit = !this.isPasswordEdit
      this.newPassword = ''
      this.reName = ''
      this.rePhone = ''
      this.passwordForCheck = ''
      this.newPasswordForCheck = ''
    },
    logout () {
      this.$store.dispatch('logout')
    },
    userUpdatePassword () {
      if (this.newPassword && this.newPasswordForCheck && this.passwordForCheck) {
        if (this.newPassword === this.newPasswordForCheck) {
          let loading = weui.loading('')
          api.userUpdatePassword({
            userId: this.userId,
            newPassword: this.newPassword,
            passwordForCheck: this.passwordForCheck
          }).then((res) => {
            loading.hide()
            if (res.status === 200) {
              weui.alert('', () => {
                this.passwordEditClick()
                this.$store.dispatch('logout')
              })
            } else {
              weui.alert(res.data.msg)
            }
          }).catch((err) => {
            loading.hide()
            console.error(err)
            weui.alert('')
          })
        } else {
          weui.alert('')
        }
      } else {
        weui.alert('')
      }
    },
    userUpdateInfo () {
      if (this.reName && this.rePhone && this.passwordForCheck) {
        let loading = weui.loading('')
        api.userUpdateInfo({
          userId: this.userId,
          reName: this.reName,
          rePhone: this.rePhone,
          passwordForCheck: this.passwordForCheck
        }).then((res) => {
          loading.hide()
          if (res.status === 200) {
            weui.toast('', 1500)
            this.userInfoGet()
            this.infoEditClick()
          } else {
            weui.alert(res.data.msg)
          }
        }).catch((err) => {
          loading.hide()
          console.error(err)
          weui.alert('')
        })
      } else {
        weui.alert('')
      }
    },
    userInfoGet () {
      api.userInfoGet({ userId: this.userId })
        .then((res) => {
          if (res.status === 200) {
            this.user = res.data.user
          } else {
            weui.alert(res.data.msg)
          }
        }).catch((err) => {
          console.error(err)
          weui.alert('')
        })
    },
    materialBookUpdateFail (materialBookId) {
      weui.actionSheet([{
        label: '',
        onClick: () => {
          let loading = weui.loading('')
          api.materialBookUpdateFail({ materialBookId })
            .then((res) => {
              loading.hide()
              if (res.status === 200) {
                weui.toast('', 1500)
                this.materialBookListGet()
              } else {
                weui.alert(res.data.msg)
              }
            }).catch((err) => {
              loading.hide()
              console.error(err)
              weui.alert('')
            })
        }
      }], [{
        label: '',
        onClick: () => {}
      }])
    },
    materialBookListGet () {
      api.materialBookListGet({ userId: this.userId })
        .then((res) => {
          if (res.status === 200) {
            this.materialBookList = res.data.materialBookList
          } else {
            this.materialBookList = []
          }
        }).catch((err) => {
          console.error(err)
          weui.alert('')
        })
    },
    meetingBookUpdateFail (meetingBookId) {
      weui.actionSheet([{
        label: '',
        onClick: () => {
          let loading = weui.loading('')
          api.meetingBookUpdateFail({ meetingBookId })
            .then((res) => {
              loading.hide()
              if (res.status === 200) {
                weui.toast('', 1500)
                this.meetingBookListGet()
              } else {
                weui.alert(res.data.msg)
              }
            }).catch((err) => {
              loading.hide()
              console.error(err)
              weui.alert('')
            })
        }
      }], [{
        label: '',
        onClick: () => {}
      }])
    },
    meetingBookListGet () {
      api.meetingBookListGet({ userId: this.userId })
        .then((res) => {
          if (res.status === 200) {
            this.meetingBookList = res.data.meetingBookList
          } else {
            this.meetingBookList = []
          }
        }).catch((err) => {
          console.error(err)
          weui.alert('')
        })
    }
  },
  beforeRouteEnter: (to, from, next) => {
    next((vm) => {
      vm.materialBookListGet()
      vm.meetingBookListGet()
    })
  },
  created () {
    this.userInfoGet()
  }
}
</script>



