<template>
  <div id="material">
    <div class="weui-cells__title"></div>
    <div class="weui-cells weui-cells_form">
      <div class="weui-cell">
        <div class="weui-cell__hd">
          <label class="weui-label"></label>
        </div>
        <div class="weui-cell__bd">
          <input class="weui-input" type="text" placeholder="" v-model="name">
        </div>
      </div>
      <div class="weui-cell">
        <div class="weui-cell__hd">
          <label class="weui-label"></label>
        </div>
        <div class="weui-cell__bd">
          <input class="weui-input" type="number" pattern="[0-9]*" placeholder="" v-model="phone">
        </div>
      </div>
      <div class="weui-cell">
        <div class="weui-cell__hd"><label class="weui-label"></label></div>
        <div class="weui-cell__bd">
          <input class="weui-input" type="text" placeholder="" v-model="activity">
        </div>
      </div>
    </div>
    <div class="weui-cells__title"></div>
    <div class="weui-cells">
      <a class="weui-cell weui-cell_access" @click="takeDatePick">
        <div class="weui-cell__hd"><label class="weui-label"></label></div>
        <div class="weui-cell__bd">
          <p>{{ takeDate }}</p>
        </div>
        <div class="weui-cell__ft"></div>
      </a>
      <a class="weui-cell weui-cell_access" @click="returnDatePick">
        <div class="weui-cell__hd"><label class="weui-label"></label></div>
        <div class="weui-cell__bd">
          <p>{{ returnDate }}</p>
        </div>
        <div class="weui-cell__ft"></div>
      </a>
    </div>
    <div class="weui-cells__title"></div>
    <div class="weui-cells">
      <a class="weui-cell weui-cell_access" @click="numberPick(index, materialBookItem, materials[materialBookItem.index].left, materials[materialBookItem.index].price)" v-for="(materialBookItem, index) in materialBookItems">
        <div class="weui-cell__hd"><label class="weui-label">{{ materials[materialBookItem.index].name }} ({{ materials[materialBookItem.index].price || 0 }})</label></div>
        <div class="weui-cell__bd">
          <p v-show="!materialBookItem.book">{{ materials[materialBookItem.index].left }}{{ materials[materialBookItem.index].unit }}</p>
          <p v-show="materialBookItem.book">{{ materialBookItem.book }}{{ materials[materialBookItem.index].unit }}</p>
        </div>
        <div class="weui-cell__ft"></div>
      </a>
      <a class="weui-cell weui-cell_access" @click="materialPick">
        <div class="weui-cell__bd">
          <p></p>
        </div>
        <div class="weui-cell__ft"></div>
      </a>
      <div class="weui-cell" v-show="sum">
        <div class="weui-cell__hd">
          <label class="weui-label"></label>
        </div>
        <div class="weui-cell__bd">
          <label class="weui-label"> {{ sum }}</label>
        </div>
      </div>
    </div>
    <div class="weui-cells__tips">17:30-18:00</div>
    <p class="weui-btn-area">
      <a @click="materialBookCreate" class="weui-btn weui-btn_primary"></a>
    </p>
  </div>
</template>

<script>
import api from '../api'
import logmsg from './logmsg'
import weui from 'weui.js'
export default {
  components: {
    logmsg
  },
  data () {
    return {
      sum: 0,
      userId: JSON.parse(window.atob(this.$store.state.token.split('.')[1])).id,
      name: '',
      phone: '',
      activity: '',
      takeDate: '',
      returnDate: '',
      materialBookItems: [],
      materials: []
    }
  },
  methods: {
    materialBookCreate () {
      if (this.name && this.phone && this.activity && this.takeDate !== '' && this.returnDate !== '' && this.returnDate !== '' && this.materialBookItems.length) {
        let loading = weui.loading('')
        api.materialBookCreate({
          materialBook: {
            userId: this.userId,
            name: this.name,
            phone: this.phone,
            activity: this.activity,
            sum: this.sum,
            takeDate: this.takeDate,
            returnDate: this.returnDate,
            remark: '',
            condition: ''
          },
          materialBookItems: this.materialBookItems,
          sum: this.sum
        }).then((res) => {
          loading.hide()
          if (res.status === 200) {
            weui.toast('', 1500)
            this.name = ''
            this.phone = ''
            this.activity = ''
            this.sum = 0
            this.takeDate = ''
            this.returnDate = ''
            this.materialBookItems = []
            this.materials = []
          } else {
            weui.alert(res.data.msg)
          }
        }).catch((err) => {
          loading.hide()
          console.error(err)
          weui.alert('')
        })
      } else {
        weui.alert('')
      }
    },
    takeDatePick () {
      const now = new Date()
      const day = ['', '', '', '', '', '', '']
      let end = new Date()
      let dateList = []
      for (let i = 0; i !== 5; ++i) {
        end.setTime(now.getTime() + 86400000 * i)
        if (end.getDay() === 1 || end.getDay() === 3 || end.getDay() === 5) {
          dateList.push({
            label: `${end.getFullYear()}${end.getMonth() + 1}${end.getDate()}${day[end.getDay()]}`,
            value: i
          })
        }
      }
      weui.picker(dateList, {
        onConfirm: (result) => {
          this.takeDate = result[0].label
          this.returnDate = ''
        },
        id: 'take-date-picker'
      })
    },
    returnDatePick () {
      if (this.returnDate !== '') {
        const day = ['', '', '', '', '', '', '']
        const yearIndex = this.takeDate.indexOf('')
        const monthIndex = this.takeDate.indexOf('')
        const dateIndex = this.takeDate.indexOf('')
        let startDate = new Date()
        startDate.setFullYear(parseInt(this.takeDate.slice(0, yearIndex)), parseInt(this.takeDate.slice(yearIndex + 1, monthIndex) - 1), parseInt(this.takeDate.slice(monthIndex + 1, dateIndex)))
        let end = new Date()
        let dateList = []
        for (let i = 0; i !== 5; ++i) {
          end.setTime(startDate.getTime() + 86400000 * i)
          if (end.getDay() === 1 || end.getDay() === 3 || end.getDay() === 5) {
            dateList.push({
              label: `${end.getFullYear()}${end.getMonth() + 1}${end.getDate()}${day[end.getDay()]}`,
              value: i
            })
          }
        }
        weui.picker(dateList, {
          onConfirm: (result) => {
            this.returnDate = result[0].label
          },
          id: 'return-date-picker'
        })
      }
    },
    numberPick (index, materialBookItem, n, price) {
      price = price || 0
      let options = []
      options.push({
        label: '',
        value: 0
      })
      for (let i = 1; i <= n; ++i) {
        options.push({
          label: i,
          value: i
        })
      }
      weui.picker(options, {
        defaultValue: [1],
        onConfirm: (result) => {
          if (result[0].value) {
            materialBookItem.book = result[0].value
            materialBookItem.sum = price * result[0].value
          } else {
            this.materialBookItems.splice(index, 1)
          }
          this.materialBookItems.unshift(0)
          this.sum = this.materialBookItems.reduce((a, b) => {
            return a + b.sum
          })
          this.materialBookItems.shift()
        },
        id: 'number-picker'
      })
    },
    materialPick () {
      let loading = weui.loading('')
      api.materialListGet()
        .then((res) => {
          if (res.status === 200) {
            this.materials = res.data.materialList
            this.materials.push({})
            let options = []
            let children = []
            for (let i = 0; i < this.materials.length - 1; ++i) {
              let isSelected = false
              for (let materialBookItem of this.materialBookItems) {
                if (materialBookItem.index === i) {
                  isSelected = true
                  break
                }
              }
              if (!isSelected) {
                children.push({
                  label: this.materials[i].name + ' (' + (this.materials[i].price || 0) + ')',
                  value: i
                })
              }
              if (this.materials[i + 1].type !== this.materials[i].type) {
                if (children.length) {
                  options.push({
                    label: this.materials[i].type,
                    children
                  })
                  children = []
                } else {
                  options.push({
                    label: this.materials[i].type,
                    children: [{
                      label: '',
                      value: -1
                    }]
                  })
                }
              }
            }
            loading.hide()
            weui.picker(options, {
              onConfirm: (result) => {
                if (result[1].value !== -1) {
                  this.materialBookItems.push({
                    index: result[1].value,
                    userId: this.userId,
                    materialId: this.materials[result[1].value]._id,
                    book: 0,
                    condition: ''
                  })
                }
              },
              id: 'material-picker'
            })
          } else {
            loading.hide()
            weui.alert(res.data.msg)
          }
        }).catch((err) => {
          loading.hide()
          console.error(err)
          weui.alert('')
        })
    }
  }
}
</script>



