<template>
  <div id="log" class="full-screen">
    <div class="weui-msg">
      <div class="weui-msg__icon-area"><i class="weui-icon-waiting weui-icon_msg"></i></div>
      <h2 class="weui-msg__title"></h2>
      <p class="weui-msg__desc">123456</p>
      <p class="weui-msg__desc"></p>
      <p class="weui-msg__desc"></p>
      <div class="weui-msg__opr-area">
        <div class="weui-cells weui-cells_form">
          <a class="weui-cell weui-cell_access" @click="departmentPick">
            <div class="weui-cell__hd"><label class="weui-label"></label></div>
            <div class="weui-cell__bd">
              <p class="department-text">{{ user.department }}</p>
            </div>
            <div class="weui-cell__ft"></div>
          </a>
          <div class="weui-cell">
            <div class="weui-cell__hd">
              <label class="weui-label"></label>
            </div>
            <div class="weui-cell__bd">
              <input class="weui-input" type="password" placeholder="" v-model="user.password">
            </div>
          </div>
        </div>
      </div>
      <div class="weui-msg__opr-area">
        <p class="weui-btn-area">
          <a @click="login" class="weui-btn weui-btn_primary"></a>
          <router-link to="/ma" class="weui-btn weui-btn_warn"></router-link>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import api from '../api'
import weui from 'weui.js'
export default {
  data () {
    return {
      user: {
        department: '',
        password: ''
      }
    }
  },
  methods: {
    login () {
      if (this.user.department !== '' && this.user.password) {
        this.$store.dispatch('login', this.user)
      }
    },
    departmentPick () {
      let loading = weui.loading('')
      api.userListGet()
        .then((res) => {
          if (res.status === 200) {
            let options = []
            let userList = res.data.userList
            userList.forEach((user, index) => {
              options.push({
                label: user.department,
                value: index
              })
            })
            loading.hide()
            weui.picker(options, {
              onConfirm: (result) => {
                this.user.department = result[0].label
              },
              id: 'user-picker'
            })
          } else {
            loading.hide()
            weui.alert(res.data.msg)
          }
        }).catch((err) => {
          loading.hide()
          console.error(err)
          weui.alert('')
        })
    }
  }
}
</script>

<style scoped>
.department-text {
  text-align: left !important;
}
</style>



